<template>
<div class="container">
  <h2 class="text-center mt-5">My Vue Todo App</h2>

  <!-- Input -->
  <div class="d-flex">
    <input v-model="task" type="text" placeholder="enter a task" class="form-control">
    <button @click="submitTask" class="btn btn-warning">SUBMIT</button>
  </div>

  <!-- Table tasks -->
  <table class="table table-bordered mt-2">
    <thead>
      <tr>
        <th>Task</th>
        <th style="width: 200px">Status</th>
        <th style="width: 100px" class="text-center">Edit</th>
        <th style="width: 100px" class="text-center">Delete</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(task, index) in tasks" :key="index">
        <td><span :class="{'finished': task.status === 'READY!'}">{{task.name}}</span></td>
        <td class="pointer " @click="changeStatus(index)"><span :class="{
          'text-danger': task.status === 'To-do',
          'text-warning': task.status === 'In-progress',
          'text-success': task.status === 'READY!'}" >{{task.status}}</span></td>
        <td class="text-center">
          <div @click="editTask(index)">
            <span class="fa fa-pen pointer"></span>
          </div>
        </td>
        <td class="text-center">
          <div @click="deleteTask(index)">
            <span class="fa fa-trash pointer"></span>
          </div>
        </td>
      </tr>
    </tbody>
  </table>
</div>
</template>

<script>
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },

  data(){
    return {
      task:  '',
      editedTask: null,
      evailableStatuses: ['To-do', 'In-progress', 'READY!'],
      tasks: [
        {
          name: 'Get ready',
          status: 'To-do'
        }
      ]
    }
  },

  methods: {
    submitTask(){
      if(this.task.length === 0) return;

      if(this.editedTask === null){
        this.tasks.push({
          name: this.task,
          status: 'To-do'
        });
      }else{
        this.tasks[this.editedTask].name = this.task;
        this.editedTask = null;
      }
      this.task = '';
    },

    deleteTask(index){
      this.tasks.splice(index, 1);
    },

    editTask(index){
      this.task = this.tasks[index].name;
      this.editedTask = index;
    },

    changeStatus(index){
      let newIndex = this.evailableStatuses.indexOf(this.tasks[index].status);
      if(++newIndex > 2) newIndex =0;
      this.tasks[index].status = this.evailableStatuses[newIndex];
    }
  }
}
</script>

<style scoped>
.pointer {
  cursor: pointer;
}

.finished {
  text-decoration: line-through;
}
</style>
